<template lang="html">
	<div class="head">
		<div class="header">
			<a href="javascript:;" class="go-back" @click="goBack">返回</a>
			<h4 class="header-cont">商品详情</h4>
		</div>
	</div>
</template>

<script>
	export default {
		methods: {
			goBack () {
				window.history.back();
			}
		}
	}
</script>

<style lang="css">
	.head {
		height: 2.2rem;
	}
	.header {
		width: 100%;
		height: 2.15rem;
		position: fixed;
		left: 0;
		top: 0;
		background-color: #f9f9f9;
		border-bottom: 0.05rem solid #e9e8e9;
	}
	.header .go-back {
		width: 2.2rem;
		height: 2.2rem;
		text-align: center;
		color: gray;
		font-size: 0.8rem;
		float: left;
		line-height: 2.2rem;
		margin-left: 0.5rem;
		position: absolute;
		left: 0;
		top: 0;
	}
	.header .header-cont {
		width: 100%;
		text-align: center;
		line-height: 2.2rem;
		font-size: 1rem;
	}
</style>



