<template lang="html">
	<div class="tab-bar">
		<ul class="tab-bar-cont">
			<li>主页</li>
			<li>发现</li>
			<li>我的</li>
		</ul>
	</div>
</template>

<script>
	export default {
	}
</script>

<style lang="css">
	.tab-bar {
		height: 2.15rem;
		width: 100%;
		position: fixed;
		bottom: 0;
		left: 0;
		border-top: 0.05rem solid #e9e8e9;
		background: #fff;
	}
	.tab-bar-cont li {
		float: left;
		width: 33.333333%;
		height: 2.2rem;
		line-height: 2.2rem;
		font-size: 1rem;
	}
	.tab-bar-cont li:hover {
		color: #12c286;
	}
	.tab-bar-cont li:active {
		color: #12c286;
	}
	.tab-bar-cont:after {
		content: '';
		display: block;
		clear: both;
		width: 0;
	}
</style>



