import Vue from 'vue'
import Router from 'vue-router'
import VueResource from 'vue-resource';
import Home from '@/pages/Home'
import Detail from '@/pages/GoodsDetail'

Vue.use(Router);
Vue.use(VueResource);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/detail/',
      name: 'Detail',
      component: Detail,
    }
  ]
})




