<template>
  <div id="container">
    <home-header></home-header>
    <div class="content">
      <ul class="cont-ul">
        <li is="list" v-for="item in items" :price="item.price" :title="item.title" :img="item.img" :key="item.id">
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import HomeHeader from '../components/HomeHeader'
  import List from '../components/HomeList'

  export default {
    name: 'home',
    data() {
      return {
        items: []
      }
    },
    components: {
      HomeHeader,
      List
    },
    created() {
       this.$http.get('../goods.json').then((data) => {   // 调用vue的ajax来请求数据，promise语法，并用es6的箭头函数
        this.items = data.body["goods"];
       })
    }
  }
</script>

<style lang="css" scoped>
  .container {
    width: 100%;
    margin: 0 auto;
  }

  .content {
    margin-bottom: 1.8rem;
  }

  .cont-ul {
    padding-top: 0.5rem;
    background-color: #f2f2f2;
  }

  .cont-ul:after {
    content: '';
    display: block;
    clear: both;
    width: 0;
    height: 0;
  }
</style>



